
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
  components: {
		Spinner
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    position: String
  },
  created () {
    this.show = this.value
  },
  data () {
    return {
      show: false,
      type:'android'
    }
  },
  watch: {
    value (val) {
      this.show = val
    },
    show (val) {
      this.$emit('input', val)
    }
  }
}



